import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { File } from '@ionic-native/file';
import { HTTP } from '@ionic-native/http';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  status: String;
  userId: String;
  saveData: string;
  http: HTTP;

  constructor(public navCtrl: NavController) {
  	this.http = new HTTP();
  }

  saveEvent(event) {
  	var file = new File();
    console.log(event);
    file.checkDir(file.dataDirectory, 'syncData').then(e => this.createFile()).catch((err) => {
    	file.createDir(file.dataDirectory, 'syncData', false);
    	this.createFile();
    });
  }

  createFile() {
  	var file = new File();
  	file.writeFile(file.dataDirectory+'/syncData', 'testdata.txt', this.saveData).then(e => {
  		this.status = "Saved";
  		file.resolveDirectoryUrl(file.dataDirectory+'/syncData').then(dE => {
	  		this.http.post('http://demo.fortunapix.com/upload', {
	  			'file' : file.getFile(dE, 'testdata.txt', {}),
	  			'user' : this.userId
	  		}, {})
	  		.then(data => {
	  			this.status = "Backup Successful";
	  		});
  		});
  	});
  }

}



